.header {
  width: 100%;
  position: fixed;
  top: 0;
  padding: 0.75% 0; /* While deploying this padding is effecting from top*/
  left: 0;
  z-index: var(--z-fixed);
  background-color: var(--body-color);
}

/* Override generic container width for header to reduce side blank space */
.header .container {
  max-width: 1200px;
}
.nav {
  height: calc(var(--header-height)+1.5rem);
  display: flex;
  justify-content: space-between;
  align-items: center;
  column-gap: 1rem;
}

.nav__logo,
.nav__toggle {
  color: var(--title-color);
  font-weight: var(--font-medium);
}

.nav__list {
  display: flex;
  column-gap: 3rem;
    /* Increased from 2rem for breathing space */
}

.nav__link {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: var(--small-font-size);
  color: var(--title-color);
  font-weight: var(--font-medium);
  transition: 0.3s;
}

.nav__icon,
.nav__close,
.nav__toggle {
  display: none;
}

/* Active Link */
.active-link,
.nav__link:hover {
  color: var(--title-color-dark);
}

/* Button reset styles for accessibility */
.nav__close,
.nav__toggle {
  background: transparent;
  border: none;
  padding: 0;
  font: inherit;
}

.nav__close:focus,
.nav__toggle:focus {
  outline: 2px solid var(--title-color);
  outline-offset: 2px;
}
/* Change background header */
.scroll-header {
  box-shadow: 0 -1px 4px rgba(0, 0, 0, 0.15);
}

/*=============== BREAKPOINTS ===============*/
@media screen and (max-width: 1080px) {
  .header {
    top: initial;
    bottom: 0;
    padding: 0 1rem;
  }

  .nav {
    height: var(--header-height);
  }

  .nav__menu {
    position: fixed;
    bottom: -100%;
    left: 0;
    width: 100%;
    background-color: var(--body-color);
    padding: 2rem 1.5rem 4rem;
    box-shadow: 0 -1px 4px rgba(0, 0, 0, 0.15);
    border-radius: 1.5rem 1.5rem 0 0;
    transition: 0.3s;
  }

  /* Show Menu */
  .show-menu {
    bottom: 0;
  }

  .nav__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }

  .nav__icon {
    font-size: 1.2rem;
  }

  .nav__close {
    position: absolute;
    right: 1.3rem;
    bottom: 0.5rem;
    font-size: 1.5rem;
    cursor: pointer;
    color: var(--title-color);
  }

  .nav__close:hover {
    color: var(--title-color-dark);
  }

  .nav__toggle {
    font-size: 1.1rem;
    cursor: pointer;
  }

  .nav__icon,
  .nav__close,
  .nav__toggle {
    display: block;
  }
}

/* For small devices */
@media screen and (max-width: 350px) {
  .nav__menu {
    padding: 2rem 0.25rem 4rem;
  }
  .nav__list {
    column-gap: 0;
  }
}
